<template>
    <div class="page">
      <h3>2.0 ()</h3>
      <pre>
        Vue.prototype.createMap = (callback) => { // 
          if (typeof (callback) !== 'function') { this.$message('') }
          if (!window.BMap) {
            let MP = _ => {
              return new Promise(function (resolve, reject) {
                window.onload = function () {
                  resolve(BMap) // eslint-disable-line
                }
                let script = document.createElement('script')
                script.type = 'text/javascript'
                script.src = `http://api.map.baidu.com/api?v=2.0&ak=${ak}&callback=init`
                script.onerror = reject
                document.head.appendChild(script)
              })
            }
            MP().then(BMap => {
              window.BMap = BMap
            })
          }
          if (!window.BMap) {
            setTimeout(_ => {
              callback() // API
            }, 1000)
          } else {
            callback() // API
          }
        }
      </pre>
      <h3>BUG  Polyline</h3>
      <pre>
        <a href="https://blog.csdn.net/dyllove98/article/details/9124353" target="_Blank">https://blog.csdn.net/dyllove98/article/details/9124353</a>
        1.
          1.1 
          1.2 
        2.

        
        var point1= new BMap.Point( 113.33765, 23.150435 )
        var point2= new BMap.Point( 113.34765, 23.160435 )
        var point3= new BMap.Point( 113.35765, 23.170435 )

        var polyline =  new BMap.Polyline([
          point1,
          point2,
          point3
        ], {strokeColor: "blue", strokeWeight: 6, strokeOpacity: 0.5})
        map.addOverlay(polyline);
      </pre>
    </div>
</template>

<script>
export default {
  name: 'baidu-map',
  data () {
    return {
      msg: 'this is baidu-map'
    }
  }
}
</script>

<style scoped>

</style>



