<template>
    <div class="page">
      <pre>
      window
      
       .gitignore. .gitignore

      sublime
      .DS_Store
      node_modules/
      /dist/
      npm-debug.log*
      yarn-debug.log*
      yarn-error.log*

      # Editor directories and files
      .idea
      .vscode
      *.suo
      *.ntvs*
      *.njsproj
      *.sln

      
      
      .gitignorepush.gitignore

      

      git rm -r --cached .
      git add .
      git commit -m 'update .gitignore'
      </pre>
    </div>
</template>

<script>
export default {
  name: 'gitignore',
  data () {
    return {
      msg: 'this is gitignore'
    }
  }
}
</script>

